@import "fonts";
@import "base";
@import "vars";

.header {
  border: 1px solid #ececec;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.17);
  &__flex {
    display: grid;
    grid-template-columns: repeat(2,max-content);
    justify-content: space-between;
    height: 100px;
    align-items: center; }
  &__left {
    display: grid;
    grid-template-columns: repeat(3,max-content);
    align-items: center; }
  &__logo {
    color: $dark;
    font-size: 27px;
    font-family: lato-light;
    text-decoration: none;
    margin-left: 13px;
    margin-right: 37px;
    cursor: pointer;
    &_other-color {
      color: $accent;
      font-family: lato-black; } }
  &__form {
    display: grid;
    grid-template-columns: repeat(3,max-content);
    &_browse {
      width: 87px;
      height: 38px;
      border-radius: 3px 0 0 3px;
      border: 1px solid #e6e6e6;
      background-color: #cdcdcc;
      border-right-color: transparent;
      background-image: linear-gradient(to bottom, #f9f9f9 0%, #f5f5f5 100%);
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
      padding-top: 2px;
      cursor: pointer;
      &:hover {
        background-color: $accent; } } }
  &__summary-search {
    display: block;
    color: $dark;
    font-size: 14px;
    line-height: 32px;
    text-align: center;
    &::-webkit-details-marker {
      display: none; }
    &::after {
      content: '';
      border-style: solid;
      border-width: 5px 4.5px 0 4.5px;
      border-color: #838383 transparent transparent transparent;
      display: inline-grid;
      margin-left: 5px;
      vertical-align: middle; }
    &:hover {
      color: $accent; } }
  &__text {
    width: 281px;
    height: 38px;
    border-radius: 0 3px 3px 0;
    border: 1px solid #e6e6e6;
    background-color: $light;
    padding-left: 16px;
    color: #a4a4a4;
    font-size: 14px;
    font-weight: 300;
    line-height: 32px;
    letter-spacing: 0.35px;
    border-radius: 0 0 0 0;
    outline: none; }
  &__button-search {
    width: 36px;
    border-radius: 0 3px 3px 0;
    border: 1px solid #e6e6e6;
    background-color: $light;
    border-left-color: transparent;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    cursor: pointer; }
  &__button-lens {
    width: 16px;
    padding-top: 4px;
    padding-right: 2px; }
  &__right {
    display: grid;
    grid-template-columns: repeat(2,max-content);
    align-items: center; } }
.header__form_browse[open] > .header__summary-search {
  &:after {
    content: '';
    border-style: solid;
    border-width: 0 4.5px 5px 4.5px;
    border-color: transparent transparent $accent transparent; } }
.header__form_browse[open] > .header__summary-search {
  &:before {
    display: none; } }
.button__logo {
  width: 130px;
  height: 38px;
  background-color: $accent;
  border: transparent;
  margin-left: 25px;
  padding-top: 2px;
  letter-spacing: 0.38px;
  color: $light;
  cursor: pointer;
  &:hover {
    box-shadow: 0 0 20px 2px $accent; } }

.dropdown__menu {
  display: grid;
  border-radius: 5px;
  background-color: $white;
  width: 262px;
  position: relative;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.17);
  z-index: 1;
  margin-top: 16px;
  &_account {
    grid-template-rows: repeat(5,max-content); }
  &_account-list {
    display: grid;
    grid-template-columns: repeat(3,max-content);
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid;
    padding: 16px 0;
    &_text {
      display: grid;
      grid-template-rows: repeat(3,1fr);
      grid-row-gap: 8px; }
    &_text-content {
      font-size: 12px;
      color: $dark;
      font-family: lato-bold;
      text-transform: uppercase; }
    &_text-content1 {
      font-size: 12px;
      color: $accent;
      text-transform: uppercase; }
    &_text-total {
      display: grid;
      grid-template-columns: repeat(2,max-content);
      justify-content: space-between;
      text-transform: uppercase;
      color: $dark; }
    &_text-total1 {
      font-size: 16px;
      font-weight: 700;
      font-family: lato-bold;
      padding: 20px 0 30px; }
    &_text-btn {
      padding: 18px 0px 18px 0px;
      text-transform: uppercase;
      font-size: 14px;
      line-height: 1.2;
      font-weight: 700;
      color: $accent;
      width: 225px;
      cursor: pointer;
      background-color: $white;
      border: 1px solid $accent;
      margin-bottom: 10px;
      &:hover {
        background-color: $accent;
        color: $white; }
      &_gray {
        border: 1px solid #eaeaea;
        color: #4a4a4a;
        &:hover {
          background-color: #eaeaea;
          color: #4a4a4a; } } } }
  &_heading {
    color: #232323;
    font-family: lato-regular;
    font-size: 14px;
    font-weight: 400;
    line-height: 32px;
    text-transform: uppercase;
    letter-spacing: 0.35px;
    border-bottom: 1px solid #e8e8e8; }
  &_item {
    font-size: 14px;
    font-weight: 400;
    line-height: 28px;
    letter-spacing: 0.35px;
    text-decoration: none;
    color: #4f4f4f;
    font-family: lato-regular;
    &:hover {
      color: $accent; } } }

.summary__basket {
  display: block;
  font-size: 14px;
  line-height: 32px;
  text-align: center;
  &::-webkit-details-marker {
    display: none; }
  &::after {
    content: '';
    border-style: solid;
    border-width: 5px 4.5px 0 4.5px;
    border-color: $light transparent transparent transparent;
    display: inline-grid;
    margin-left: 5px;
    vertical-align: middle; } }
.button__logo[open] > .summary__basket {
  &:after {
    content: '';
    border-style: solid;
    border-width: 0 4.5px 5px 4.5px;
    border-color: transparent transparent $light transparent; } }
.button__logo[open] > .summary__basket {
  &:before {
    display: none; } }
.header-nav {
  display: grid;
  align-items: center;
  height: 51px; }
.menu {
  display: grid;
  grid-template-columns: repeat(7,max-content);
  grid-column-gap: 50px;
  justify-items: center;
  color: $dark;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  &__list {
    list-style-type: none;
    position: relative;
    cursor: pointer;
    &:hover .mega__box {
      display: grid;
      grid-template-columns: repeat(3,235px);
      grid-column-gap: 46px; }
    &_last {
      &:hover .mega__box {
        right: 0;
        left: auto; }
      &:hover .mega__box::before {
        right: 26px;
        left: auto; } } } }
.mega__box {
  display: none;
  border: 1px solid #e8e8e8;
  background-color: $white;
  padding: 20px;
  border-radius: 5px;
  position: absolute;
  margin-top: 5px;
  z-index: 1;
  &:before {
    content: '';
    display: block;
    width: 12px;
    height: 12px;
    background-color: $white;
    position: absolute;
    transform: rotate(45deg);
    top: -7px;
    left: 20px;
    border-left: 1px solid #e8e8e8;
    border-top: 1px solid #e8e8e8; }
  &__heading {
    border-bottom: 1px solid #e8e8e8;
    margin-bottom: 8px;
    font-size: 14px;
    text-transform: uppercase;
    padding-bottom: 8px; }
  &__heading1 {
    padding-bottom: 8px;
    padding-top: 20px; }
  &__item {
    display: grid;
    grid-template-columns: 235px;
    &:last-of-type {
      margin-right: 0; } }
  &__link {
    text-decoration: none;
    color: #646464;
    font-size: 14px;
    font-weight: 400;
    line-height: 28px;
    letter-spacing: 0.35px;
    text-transform: capitalize;
    &:hover {
      color: $accent; } }
  &__link-img {
    padding-top: 20px;
    position: relative;
    padding-bottom: 4px;
    right: 20px; }
  &__link-img-sale {
    font-size: 22px;
    line-height: 28px;
    font-weight: 900;
    text-transform: uppercase;
    letter-spacing: 0.025em;
    color: $white;
    position: absolute;
    right: 35px;
    bottom: 35px; } }

.banner {
  background-color: #e8e8e8;
