.radiobtn {
  position: absolute;
  z-index: -1;
  opacity: 0;
  + label {
    position: relative;
    padding: 0 0 0 30px;
    cursor: pointer;
    text-transform: uppercase;
    font-size: 12px;
    &:before {
      content: " ";
      position: absolute;
      top: 1px;
      left: 0;
      width: 14px;
      height: 14px;
      border-radius: 50%;
      border: 2px solid #555555;
      background: #ffffff; }
    &:after {
      content: " ";
      position: absolute;
      top: 5px;
      left: 4px;
      width: 6px;
      height: 6px;
      border-radius: 50px;
      background: #555555;
      box-shadow: 0px 0px 2px 1px #767676 inset;
      opacity: 0;
      transition: .2s; } }
  &:checked + label:after {
    opacity: 1; }
  &:focus + label:before {
    box-shadow: 0px 0px 4px 1px #4d4438; } }

//
// checkbox
//
.control {
  font-family: lato;
  display: block;
  position: relative;
  padding-left: 25px;
  margin-bottom: 5px;
  padding-top: 1px;
  cursor: pointer;
  input {
    position: absolute;
    z-index: -1;
    opacity: 0; } }

.control_indicator {
  position: absolute;
  top: 3px;
  left: 0;
  height: 13px;
  width: 13px;
  background: #ffffff;
  border: 1px solid #ebebeb;
  border-radius: 0px; }

.control {
  &:hover input ~ .control_indicator {
    background: #cccccc; }

  input {
    &:focus ~ .control_indicator {
      background: #cccccc; }

    &:checked ~ .control_indicator {
      background: #f16d7f; } }

  &:hover input:not([disabled]):checked ~ .control_indicator {
    background: #e6647d; }

  input {
    &:checked:focus ~ .control_indicator {
      background: #e6647d; }

    &:disabled ~ .control_indicator {
      background: #e6e6e6;
      opacity: 0.6;
      pointer-events: none; } } }

.control_indicator:after {
  box-sizing: unset;
  content: '';
  position: absolute;
  display: none; }

.control input:checked ~ .control_indicator:after {
  display: block; }

.control-checkbox {
  .control_indicator:after {
    left: 4px;
    top: 1px;
    width: 2px;
    height: 5px;
    border: solid #ffffff;
    border-width: 0 2px 2px 0;
    transform: rotate(45deg); }

  input:disabled ~ .control_indicator:after {
    border-color: #7b7b7b; } }
//
//range
//
//

input[type=range] {
  -webkit-appearance: none;
  margin: 10px 0;
  width: 100%;

  &:focus {
    outline: none; }

  &::-webkit-slider-runnable-track {
    width: 100%;
    height: 6px;
    cursor: pointer;
    animate: 0.2s;
    box-shadow: 0px 0px 0px #000000;
    background: #f0f0f0;
    border-radius: 3px;
    border: 0px solid #000000; }

  &::-webkit-slider-thumb {
    box-shadow: 0px 0px 1px #000000;
    border: 0px solid #000000;
    height: 18px;
    width: 18px;
    border-radius: 10px;
    background: #f16d7f;
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -6px; }

  &:focus::-webkit-slider-runnable-track {
    background: #f0f0f0; }

  &::-moz-range-track {
    width: 100%;
    height: 6px;
    cursor: pointer;
    animate: 0.2s;
    box-shadow: 0px 0px 0px #000000;
    background: #f0f0f0;
    border-radius: 3px;
    border: 0px solid #000000; }

  &::-moz-range-thumb {
    box-shadow: 0px 0px 1px #000000;
    border: 0px solid #000000;
    height: 18px;
    width: 18px;
    border-radius: 10px;
    background: #f16d7f;
    cursor: pointer; }

  &::-ms-track {
    width: 100%;
    height: 6px;
    cursor: pointer;
    animate: 0.2s;
    background: transparent;
    border-color: transparent;
    color: transparent; }

  &::-ms-fill-lower, &::-ms-fill-upper {
    background: #f0f0f0;
    border: 0px solid #000000;
    border-radius: 6px;
    box-shadow: 0px 0px 0px #000000; }

  &::-ms-thumb {
    box-shadow: 0px 0px 1px #000000;
    border: 0px solid #000000;
    height: 18px;
    width: 18px;
    border-radius: 10px;
    background: #f16d7f;
    cursor: pointer; }

  &:focus {
    &::-ms-fill-lower, &::-ms-fill-upper {
      background: #f0f0f0; } } }

